import { lazy } from 'react';

import type { BlockComponentProps, BlocksRendererProps } from '~/libs/notion';

import { Heading } from './Heading';
import { List, ListItem } from './List';
import { Paragraph } from './Paragraph';
import { Quote } from './Quote';

const Code = lazy(() =>
  import('./Code').then((component) => ({ default: component.Code })),
);
const Embed = lazy(() =>
  import('./Embed').then((component) => ({ default: component.Embed })),
);
const Image = lazy(() =>
  import('./Image').then((component) => ({ default: component.Image })),
);

const componentMap = {
  paragraph: Paragraph,
  heading_1: Heading,
  heading_2: Heading,
  heading_3: Heading,
  code: Code,
  quote: Quote,
  bulleted_list: List,
  bulleted_list_item: ListItem,
  numbered_list: List,
  numbered_list_item: ListItem,
  image: Image,
  embed: Embed,
};

type BlockComponent = (props: BlockComponentProps) => React.ReactElement;

export function BlocksRenderer({ blocks, imageBasePath }: BlocksRendererProps) {
  return (
    <>
      {blocks.map(({ block, children }, index) => {
        if (!('type' in block)) return null;

        const mapKey = block.type as keyof typeof componentMap;
        const Component = componentMap[mapKey];
        const previousBlockType =
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          index > 0 ? blocks[index - 1].block?.type : null;

        if (!Component) return null;
        const BlockComponent = Component as BlockComponent;

        return (
          <BlockComponent
            basePath={imageBasePath}
            block={block}
            key={block.id}
            previousBlockType={previousBlockType}
          >
            {children?.length ? <BlocksRenderer blocks={children} /> : null}
          </BlockComponent>
        );
      })}
    </>
  );
}
